.Todo {
  padding: 0.5em;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  -ms-flex-align: center;
      align-items: center;
  border-bottom: 1px solid #ccc;
  cursor: pointer;
}

.TodoTitle {
  text-align: left;
  -ms-flex-positive: 1;
      flex-grow: 1;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  padding: 0.5em;
}

.TodoDate {
  -ms-flex-positive: 0;
      flex-grow: 0;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  font-size: 85%;
  padding: 0.5em;
  color: lightslategray;
}

.DeleteTodoBtn,
.EditTodoBtn,
.ToggleTodoBtn {
  padding: 0.5em;
  -ms-flex-negative: 0;
      flex-shrink: 0;
  color: lightslategray;
  cursor: pointer;
}

.Todo.complete .TodoTitle {
  color: lightgray;
  font-style: italic;
  text-decoration: line-through;
}

.Todo.complete .ToggleTodoBtn {
  color: lightgray;
}



